}.App {
      text-align: center;
      }.App-logo {
        -webkit-animation: App-logo-spin infinite 8s linear;
                animation: App-logo-spin infinite 8s linear;
        height: 80px;
      }#logo {
        display: inline-block;
        margin: 0 auto;
        position: absolute;
      }@-webkit-keyframes App-logo-spin {
        from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
        50%  { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
        100% { -webkit-transform: rotate3d(0, 1, 0, 180deg); transform: rotate3d(0, 1, 0, 180deg); }
      }@keyframes App-logo-spin {
        from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
        50%  { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
        100% { -webkit-transform: rotate3d(0, 1, 0, 180deg); transform: rotate3d(0, 1, 0, 180deg); }
      }                 
/*Construction Component*/
#uC {
    text-align: center;
    text-fill-color: linear-gradient(yellow, black);
    text-shadow:
    -2px -2px 0 #804000,
    2px -2px 0 #ff8000,
    -2px 2px 0 #804000,
    2px 2px 0 #663300;
    -webkit-animation: construction 4s linear 3s infinite alternate;
            animation: construction 4s linear 3s infinite alternate;
}@-webkit-keyframes construction {
  0% { color: orange-red; left: 100px; }
  100% {color: orange; left: 20%; }
}@keyframes construction {
  0% { color: orange-red; left: 100px; }
  100% {color: orange; left: 20%; }
}
